.app {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  padding: 20px;
  background-color: #f9f9f9;
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.controls {
  margin: 20px 0;
}

button {
  margin: 5px;
  padding: 10px 15px;
  font-size: 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  transition: background 0.2s;
}

button:hover {
  background-color: #0056b3;
}

.player-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.player-card {
  background: white;
  border-radius: 10px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
  padding: 15px;
  width: 220px;
  text-align: center;
}

.player-card h3 {
  margin-bottom: 10px;
  font-size: 18px;
}

.player-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.player-info img {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}

.result-section {
  margin-top: 15px;
  font-size: 14px;
  line-height: 1.5;
}

.result-section span {
  display: block;
}
